/* eslint-disable no-shadow */
import { Controller } from '@ali/4ever-cangjie';
import {
  TABLE_ADJUST_COLS_WIDTH,
  TABLE_SET_MIN_COLS_WIDTH_DATA,
} from '../actions';
import setTableMinColsWidthData from '../commands/setTableMinColsWidthData';
import adjustColsWidth from '../commands/adjustColsWidth';

export default function onAction(action, controller: Controller, next) {
  const { type, payload } = action;
  if (type === TABLE_SET_MIN_COLS_WIDTH_DATA) {
    const { data } = payload;
    return setTableMinColsWidthData(controller, data);
  }

  if (type === TABLE_ADJUST_COLS_WIDTH) {
    return adjustColsWidth(controller, payload.node, payload.containerWidth);
  }

  return next();
}
